import React from "react";
import "../styles/home.css";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
import { Container } from 'react-bootstrap'

const HomeScreen = ({ load }) => {
  const navigate = useNavigate();

  const handleToWork = () => {
    navigate("/Victoria_Stewart_Photography/work");
  };

  return (
    <>
      <motion.div
        variants={load}
        initial="initial"
        animate="animate"
        className="background-image text-center"
      >
        <motion.h2
          variants={load}
          initial="initialUp"
          animate="animateUp"
          className="mb-4"
        >
          Portrait Photographer based in Murray, Kentucky
        </motion.h2>
        <motion.button
          variants={load}
          initial="initialUp"
          animate="animateUp"
          id="see-portfolio-btn"
          onClick={handleToWork}
        >
          View Portfolio
        </motion.button>
      </motion.div>
    </>
  );
};

export default HomeScreen;
