import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { motion } from "framer-motion";
import "../styles/work.css";
import { useNavigate } from "react-router-dom";

import engagement from "../images/engagement/couple.jpg";
import portrait from "../images/portrait/tory.jpeg";
import wedding from "../images/wedding/kiss.jpeg";
import maternity from "../images/maternity/sand.jpg";

const imageVariants = {
  hidden: {
    opactiy: 1,
  },
  visible: {
    opacity: 0.8,
  },
};

const WorkScreen = ({ load }) => {
  const navigate = useNavigate();

  const handleToEngagement = () => {
    navigate("/Victoria_Stewart_Photography/engagement");
  };

  const handleToWedding = () => {
    navigate("/Victoria_Stewart_Photography/wedding");
  };

  const handleToMaternity = () => {
    navigate("/Victoria_Stewart_Photography/maternity");
  };

  const handleToPortrait = () => {
    navigate("/Victoria_Stewart_Photography/portrait");
  };

  return (
    <Container className="mt-5">
      <motion.div
        variants={load}
        initial="initial"
        animate="animate"
        className="row work-row my-3 flex-wrap"
      >
        <Col sm={12} lg={5} md={8} className="work-card-col" id="portrait-card">
          {/* <button onClick={handleToEngagement}> */}
            <h3 onClick={handleToEngagement} className="work-card-title">Engagement</h3>
            <motion.img
            onClick={handleToEngagement}
              src={engagement}
              alt="couple smiling"
              id="engagement-image"
              variants={imageVariants}
              initial="hidden"
              whileHover="visible"
            ></motion.img>
          {/* </button> */}
        </Col>
        <Col sm={12} lg={5} md={8} className="work-card-col" id="portrait-card">
          {/* <button onClick={handleToWedding}> */}
            <h3 onClick={handleToWedding} className="work-card-title">Wedding</h3>
            <motion.img
            onClick={handleToWedding}
              src={wedding}
              alt="couple smiling"
              id="engagement-image"
              variants={imageVariants}
              initial="hidden"
              whileHover="visible"
            ></motion.img>
          {/* </button> */}
        </Col>
        <Col sm={12} lg={5} md={8} className="work-card-col" id="portrait-card">
          {/* <button onClick={handleToPortrait}> */}
            <h3 onClick={handleToPortrait} className="work-card-title">Portrait</h3>
            <motion.img
            onClick={handleToPortrait}
              src={portrait}
              alt="couple smiling"
              id="engagement-image"
              variants={imageVariants}
              initial="hidden"
              whileHover="visible"
            ></motion.img>
          {/* </button> */}
        </Col>
        <Col sm={12} lg={5} md={8} className="work-card-col" id="portrait-card">
            <h3 onClick={handleToMaternity} className="work-card-title">Maternity</h3>
            <motion.img
            onClick={handleToMaternity}
              src={maternity}
              alt="couple smiling"
              id="engagement-image"
              variants={imageVariants}
              initial="hidden"
              whileHover="visible"
            ></motion.img>
        </Col>
      </motion.div>
    </Container>
  );
};

export default WorkScreen;
